.pull-panel {
}

.pull-header {
  position: absolute;
  z-index: 2;
  top: 0;
  left: 0;
  right: 0;
}

.pull-footer {
  position: absolute;
  z-index: 2;
  bottom: 0;
  left: 0;
  right: 0;
}

.pull-body {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.pull-wrapper {
  position: absolute;
  z-index: 1;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: hidden;
}

.pull-scroller {
  position: absolute;
  left: 0;
  right: 0;
  z-index: 1;
  user-select: none;
  text-size-adjust: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-touch-callout: none;
}

.pull-refresh {
  position: absolute;
  top: -40px;
  left: 0;
  right: 0;
  display: block;
  overflow: hidden;
}

.pull-refresh-icon-wrapper {
  position: relative;
  width: 40px;
  margin: auto;
  text-align: center;
  height: 25px;
}

.pull-refresh-icon {
  position: absolute;
  top: 0;
  left: 12px;
  width: 14px;
  height: 25px;
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  background-image: url(data:image/svg+xml;base64,);
}

.pull-refresh-icon.rotate {
  transform: translate3d(0, 0, 0) rotate(180deg);
}

.pull-refresh-icon.loading {
  animation: pull-loading 0.8s infinite linear;
  background-image: url(data:image/svg+xml;base64,);
}

@keyframes pull-loading {
  from {
    transform: translate3d(0, 0, 0) rotate(0deg);
  }
  to {
    transform: translate3d(0, 0, 0) rotate(360deg);
  }
}

.pull-refresh-text {
  color: #666;
  font-size: 12px;
  text-align: center;
}

// 加载更多
.pull-load-more {
  position: absolute;
  bottom: 5px;
  left: 0;
  right: 0;
  visibility: hidden;
}

.pull-load-more-icon {
  height: 20px;
  width: 20px;
  margin: 0 auto;
  color: #666;
  background-size: contain;
  background: url(data:image/svg+xml;base64,) center no-repeat;
  transform: rotate(180deg);
}

.pull-load-more-icon.hide {
  display: none;
}

.pull-load-more-icon.rotate {
  transform: rotate(0deg);
}

.pull-load-more-icon.loading {
  display: block;
  background: transparent url('data:image/svg+xml;charset=utf-8,<svg viewBox=\'0 0 120 120\' xmlns=\'http://www.w3.org/2000/svg\' xmlns:xlink=\'http://www.w3.org/1999/xlink\'><defs><line id=\'l\' x1=\'60\' x2=\'60\' y1=\'7\' y2=\'27\' stroke=\'%236c6c6c\' stroke-width=\'11\' stroke-linecap=\'round\'/></defs><g><use xlink:href=\'%23l\' opacity=\'.27\'/><use xlink:href=\'%23l\' opacity=\'.27\' transform=\'rotate(30 60,60)\'/><use xlink:href=\'%23l\' opacity=\'.27\' transform=\'rotate(60 60,60)\'/><use xlink:href=\'%23l\' opacity=\'.27\' transform=\'rotate(90 60,60)\'/><use xlink:href=\'%23l\' opacity=\'.27\' transform=\'rotate(120 60,60)\'/><use xlink:href=\'%23l\' opacity=\'.27\' transform=\'rotate(150 60,60)\'/><use xlink:href=\'%23l\' opacity=\'.37\' transform=\'rotate(180 60,60)\'/><use xlink:href=\'%23l\' opacity=\'.46\' transform=\'rotate(210 60,60)\'/><use xlink:href=\'%23l\' opacity=\'.56\' transform=\'rotate(240 60,60)\'/><use xlink:href=\'%23l\' opacity=\'.66\' transform=\'rotate(270 60,60)\'/><use xlink:href=\'%23l\' opacity=\'.75\' transform=\'rotate(300 60,60)\'/><use xlink:href=\'%23l\' opacity=\'.85\' transform=\'rotate(330 60,60)\'/></g></svg>') center no-repeat;
  background-size: 20px 20px;
  animation: pull-load-more 1s step-end infinite;
}

.pull-load-more-text {
  font-size: 12px;
  margin-top: 5px;
  color: #666;
  text-align: center;
}

@keyframes pull-load-more {
  0% {
    transform: rotate(0deg)
  }
  8.33333333% {
    transform: rotate(30deg)
  }
  16.66666667% {
    transform: rotate(60deg)
  }
  25% {
    transform: rotate(90deg)
  }
  33.33333333% {
    transform: rotate(120deg)
  }
  41.66666667% {
    transform: rotate(150deg)
  }
  50% {
    transform: rotate(180deg)
  }
  58.33333333% {
    transform: rotate(210deg)
  }
  66.66666667% {
    transform: rotate(240deg)
  }
  75% {
    transform: rotate(270deg)
  }
  83.33333333% {
    transform: rotate(300deg)
  }
  91.66666667% {
    transform: rotate(330deg)
  }
  100% {
    transform: rotate(360deg)
  }
}
